import Vue from 'vue'
import App from './App.vue'
import animate from 'animate.css'
import {  Message,MessageBox } from 'element-ui'
Vue.use(animate)
Vue.use(MessageBox)
Vue.use(Message)
Vue.config.productionTip = false
Vue.prototype.$message = Message
Vue.prototype.$msgbox = MessageBox
new Vue({
  render: h => h(App),
}).$mount('#app')
