<script>

export default {
  name: 'app',
  components: {

  },
data () {
    return {
	tips:[	{icon:"./assets/tip1.png",tip:"代办服务"},
			{icon:"./assets/tip1.png",tip:"二手交易"},
			{icon:"./assets/tip1.png",tip:"互助问答"}
		],
	step: 0,
	showpage: 2,
	show1: false,
	showtip: false,
	showshadow: false,
	}
	},

methods: {
	clickpage1: function  (event) {
		    console.log('page1clicked',event);
			//if(this.show1 && this.step === 2)
			{
					this.showpage = 2;
					this.showshadow = false;
			}
	},
	clickpage2: function  (event) {
		    console.log('page2clicked',event);
			{
					this.showpage = 3;
			}
	},
    logoclicked: function (event) {
    console.log('logoclicked',event);
	this.show1 = true;
	this.showshadow = true;
	//this.step += 1;
	},
	join: function  (event) {
		//get xxx
		let xxx = 100;
		const h = this.$createElement;
		console.log(h);
        this.$msgbox({
          title: 'seeker 海报',
		  center: true,
          message: h('p', null, [
            h('span', null, '我已成为第'+xxx+'位seeker'),
          ]),
          showCancelButton: true,
          confirmButtonText: '分享',
          cancelButtonText: '关闭',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '分享中...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
    beforeEnter: function (el) {
      //console.log('打印dom元素',el)
      //el.style.animationDelay = "1s"
    },
	beforeEnter2: function (el) {
	  //console.log('打印dom元素',el)
	  el.style.animationDelay = "0.5s"
	},
	beforeLeave: function (el) {
      //console.log('打印dom元素',el)
      el.style.animationDelay = "0.2s"
    },
	beforeLeave2: function (el) {
	  //console.log('打印dom元素',el)
	  el.style.animationDelay = "0.2s"
	},
    enter: function  (el, done) {
      let delayNum = '1.'+el.dataset.index * 1500 + 's'
      el.style.animationDelay = delayNum
      //console.log(el.dataset.index,'输出值效果', delayNum, el.dataset, el, done)
    }
}
}
</script>

